@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Serif:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  margin: 0;
}


.home-banner{
    background-image: url(/lexoj.al/static/media/background.daff393e.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    text-align: center;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}
.banner-title{
    font-weight: bold;
    font-size: 48px;
}
.banner-action{
    background-color: white;
    color: black;
    font-size: 20px;
    border-radius: 5px;
    padding: 10px 10px;
    cursor: pointer;
}
.banner-action:hover{
    background-color: rgb(190, 190, 190);
}
.books-container-home{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 0fr;
    padding: 10px 20px;
}

.books-section{
    border-top: 7px solid rgb(100, 100, 100);
    padding: 10px 0;
    background-color: #0F1111;
}

.books-section p{
    margin: 0;
}

.section-h{
    font-family: 'Merriweather', serif;
    font-size: 28px;
    padding: 10px 0px 0px 0px;
    color: white;
    text-align: center;
}
.section-p{
    font-family: 'PT Serif', serif;
    font-size: 24px;
    color: rgb(150, 150, 150);
    text-align: center;
}

.section2{
    padding: 20px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}
.section2 div{
    width: 35%;
}
.section2 img{
    width: 35%;
    height: auto;
    min-width: 400px;
}
.section2 p{
    text-align: start;
}
.section2 .section-h{
    font-size: 48px;
    letter-spacing: 5px;
}
.section2 .section-p{
    font-size: 28px;
}

@media screen and (max-width: 1000px) {
    .books-container-home{
        grid-template-columns: 1fr 1fr 0fr;
    }
    .section2{
        flex-direction: column;
        justify-content: space-between;
    }
    .section2 div{
        width: 80%;
        margin-bottom: 20px;
    }
}

@media screen and (max-width: 600px) {
    .books-container-home{
        grid-template-columns: 1fr 0fr;
    }
    .section2 img{
        min-width: 270px;
    }
}
.book-card{
    margin: 10px 0;
    width: 175px;
    height: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    transition: .5s;
}
.book-card:hover{
    transform: rotate(5deg);
}
.book-card img{
    width: 100%;
    height: 75%;
}
.card-details{
    width: 100%;
    height: 25%;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.card-title{
    margin: 0;
    width: 100%;
    height: 60%;
    overflow: hidden;
    font-weight: bold;
    color: rgb(230, 230, 230);
    letter-spacing: 1px;
}
.card-subdetails{
    width: 100%;
    height: 40%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    color: rgb(200, 200, 200);
}
.card-author{
    overflow: hidden;
    margin: 0;
    width: 70%;
    height: 100%;
}
.card-views{
    margin: 0;
    width: 30%;
    height: 100%;
    text-align: end;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.card-views p{
    margin: 0;
    width: 50%;
    height: 100%;
}
.card-icon{
    padding-top: 2px;
}

@media screen and (max-width: 600px){
    .book-card{
        width: 150px;
    }
    .card-author{
        font-size: 14px;
    }
}
.pagination{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 20px 0;
}
.pagination p{
    color: white;
    margin: 0 20px;
    font-size: 20px;
}
.arrow{
    cursor: pointer;
    padding-top: 5px;
}
.books-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 0fr;
    padding: 0 20px;
}
.browse-screen{
    background-color: #0F1111;
    padding-top: 70px;
}
.categories-container{
    color: rgb(170, 170, 170);
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 0fr;
    padding: 10px 20px;
    line-height: 5px;
    box-shadow: inset 0 0 10px #000000;
}
.category{
    cursor: pointer;
    font-size: 18px;
}
.category:hover, .categories-triggers p:hover{
    color:  white;
}
.categories-triggers{
    padding: 10px 20px 0 20px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    color:  rgb(170, 170, 170);
    font-size: 20px;
}
.categories-triggers p{
    cursor: pointer;
}
@media screen and (max-width: 1000px) {
   .books-container{
        grid-template-columns: 1fr 1fr 0fr;
   }
   .categories-container{
        grid-template-columns: 1fr 1fr 1fr 0fr;
   }
}

@media screen and (max-width: 600px) {
    .books-container{
        grid-template-columns: 1fr 0fr;
   }
   .categories-container{
        grid-template-columns: 1fr 1fr 0fr;
    }
}
.book-screen{
    background-color: #0F1111;
    padding-top: 70px;
}
.book-info{
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.book-cover{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.book-cover img{
    width: 50%;
    height: auto;
}
.book-details{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}
.book-title{
    margin: 0;
    font-family: 'Roboto Slab', serif;
    color: white;
    font-size: 30px;
}
.book-details div .book-detail{
    display: grid;
    grid-template-columns: 1fr 4fr;
    font-family: 'Roboto', sans-serif;
    color: white;
    font-size: 18px;
}

.book-details div span{
    font-family: 'Roboto Slab', serif;
    color: white;
    font-size: 18px;
    letter-spacing: 1px;
    cursor: pointer;
}
.book-description{
    font-family: 'Roboto', sans-serif;
    color: rgb(201, 201, 201);
    font-size: 17px;
    line-height: 20px;
}

.book-cover p{
    border: 1px solid white;
    border-radius: 5px;
    color: white;
    width: 50%;
    text-align: center;
    font-size: 24px;
    padding: 5px 0;
    align-items: center;
    cursor: pointer;
}
.book-cover p:hover{
    background-color: white;
    color: black;
}
@media screen and (max-width: 800px) {
    .book-info{
        flex-direction: column;
    }
    .book-cover{
        width: 100%;
    }
    .book-details{
        width: 100%;
    }
    .book-details div .book-detail{
        grid-template-columns: 1fr 2fr;
    }
}
@media screen and (max-width: 600px) {
    .book-cover p{
        width: 80%;
    }
    .book-cover img{
        width: 80%;
    }
}
.sign-screen{
    height: 90vh;
    padding-top: 70px;
    background-color: #0F1111;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.logo-side{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
}
.logo-side img{
    width: 50%;
    height: auto;
}
.sign-side{
    box-shadow: inset 0 0 5px #000000;
    background-color: gray;
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.sign-card{
    border: 1px solid rgb(200, 200, 200);
    border-radius: 5px;
    width: 75%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 10vh 0;
}
.sign-card p{
    font-weight: bold;
    font-size: 24px;
    margin: 0;
}
.sign-card input {
    color: #0F1111;
    font-size: 20px;
    padding: 10px;
    margin: 15px 0;
    width: 50%;
    fill: none;
    background: transparent;
    outline: none;
    border: none;
    border-bottom: 1px solid rgb(200, 200, 200);
}
input::-webkit-input-placeholder{
    color: #2c3131;
}
input:-ms-input-placeholder{
    color: #2c3131;
}
input::-ms-input-placeholder{
    color: #2c3131;
}
input::placeholder{
    color: #2c3131;
}
.sign-card button{
    color: white;
    background-color: #0F1111;
    font-size: 20px;
    height: 40px;
    margin-top: 20px;
    width: 50%;
}
@media screen and (max-width: 600px) {
    .logo-side{
        display: none;
    }
    .sign-side{
        width: 100%;
    }
}
.header-bar{
    position: fixed;
    top: 0;
    left: 0;
    width: 97%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    height: 70px;
    background-color: rgba(0, 0, 0, 0.1);
    -webkit-backdrop-filter: blur(5px) !important;
            backdrop-filter: blur(5px) !important;
    z-index: 2;
}
.header-bar img{
    width: 150px;
    height: 50px;
}
.search-bar{
    width: 250px;
    height: 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    border: 2px solid  rgb(255, 255, 255);
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 20px;
}
.search-bar input{
    fill: none;
    background: transparent;
    outline: none;
    border: none;
    width: 80%;
    font-size: 16px;
}
.log-bar{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.log-bar div{
    width: 120px;
    border: 1px solid rgb(255, 255, 255);
    text-align: center;
    font-size: 18px;
    border-radius: 5px;
    padding: 5px 0;
    margin: 0 0 0 10px;
    cursor: pointer;
    color: white;
}
.log-bar div:hover{
    background-color: white;
    color: black;
}
@media screen and (max-width: 800px){
    .log-bar{
        display: none;
    }
    .search-bar{
        width: 40%;
        margin-left: auto;
        margin-right: auto;
    }
    .header-bar{
        background-color: rgba(0, 0, 0, 0.705);
    }
}
